import Vue from 'vue';
import Router from 'vue-router';
import Dashboard from '@/components/Dashboard';
import NewCafe from '@/components/NewCafe';
import ViewCafe from '@/components/ViewCafe';
import EditCafe from '@/components/EditCafe';
import Login from '@/components/Login';
import Register from '@/components/Register';
import firebase from 'firebase';

Vue.use(Router)

let router = new Router({
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard,
      meta:{
        requiresAuth: true
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta:{
        requiresGuest: true
      }
    },
    {
      path: '/register',
      name: 'register',
      component: Register,
      meta:{
        requiresGuest: true
      }
    },    
    {
      path: '/new',
      name: 'new-cafe',
      component: NewCafe,
      meta:{
        requiresAuth: true
      }
    },    
    {
      path: '/edit/:cafe_id',
      name: 'edit-cafe',
      component: EditCafe,
      meta:{
        requiresAuth: true
      }
    },    
    {
      path: '/:cafe_id',
      name: 'view-cafe',
      component: ViewCafe,
      meta:{
        requiresAuth: true
      }
    }    
  ]
});

//Nav Guards
router.beforeEach((to, from, next) => {
  //check for requiredAuth guard
  if(to.matched.some(record => record.meta.requiresAuth)){
    //check if NOT logged in
    if(!firebase.auth().currentUser){
      //Go to login
      next({
        path: '/login',
        query: {
          redirect: to.fullPath
        }
      });
    }else{
      //Process to route
      next();
    }
  } else if(to.matched.some(record => record.meta.requiresGuest)){
    //check if logged in
    if(firebase.auth().currentUser){
      //Go to login
      next({
        path: '/',
        query: {
          redirect: to.fullPath
        }
      });
    }else{
      //Process to route
      next();
    }
  }else{
    //Process to route
    next();
  }
});

export default router;


