<template>
    <div id="dashboard">        
        <ul class="collection with-header">
            <li class="collection-header">
                <h4>Cafes List</h4>
            </li>
            <li v-for="cafe in cafes" v-bind:key="cafe.cafe_id" class="collection-item">
                #{{cafe.cafe_id}} 
                <span class="brown-text"><i class="fa fa-coffee"></i> {{cafe.name}}</span>
                <i class="fa fa-map-marker"></i> {{cafe.address}} 
                <div class="chip"><i class="fa fa-train"></i> {{cafe.station}}</div>
                <router-link class="secondary-content" v-bind:to= "{name:'view-cafe', params: {cafe_id: cafe.cafe_id}}"><i class="fa fa-eye"></i></router-link>
            </li>
        </ul>
        <div class="fixed-action-btn">
            <router-link to="/new" class="btn-floating btn-large brown">
            <i class="fa fa-plus"></i>
            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'dashboard',
    data () {
        return{
            cafes: []            
        } 
    },
    created(){
        db.collection('cafes').orderBy('name').get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                const data = {
                    'id': doc.id,
                    'cafe_id': doc.data().cafe_id,
                    'address': doc.data().address,
                    'name': doc.data().name,
                    'station': doc.data().station
                }
                this.cafes.push(data)
            })
        })
    }
}
</script>


