<template>
    <div id="edit-cafe">
        <h4>Edit Cafe</h4>
        <div class="row">
            <form @submit.prevent="updateCafe" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input disabled type="text" v-model="cafe_id" required>
                    </div>
                </div>                
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="address" required>
                    </div>
                </div>    
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="station" required>
                    </div>
                </div>    
                <button type="submit" class="btn brown">Submit</button>   
                <router-link to="/" class="btn brown">Cancel</router-link>     
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'edit-cafe',
    data () {
        return{
            cafe_id: null,
            name: null,
            address: null,
            station: null
        }
    },
    beforeRouteEnter(to, from, next){
        db.collection('cafes').where('cafe_id', '==', to.params.cafe_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.cafe_id = doc.data().cafe_id;
                    vm.address = doc.data().address,
                    vm.name = doc.data().name,
                    vm.station = doc.data().station
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData(){
            db.collection('cafes').where('cafe_id', '==', this.$route.params.cafe_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.cafe_id = doc.data().cafe_id;
                    this.address = doc.data().address,
                    this.name = doc.data().name,
                    this.station = doc.data().station
                })
            })
        },
        updateCafe(){
             db.collection('cafes').where('cafe_id', '==', this.$route.params.cafe_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.update({
                        cafe_id: this.cafe_id,
                        name: this.name,
                        address: this.address,
                        station: this.station
                    })
                    .then(() => {
                        this.$router.push({name: 'view-cafe', params: {cafe_id: this.cafe_id}})
                    })
                })
            })   
        }
    }    
}
</script>


