<template>
    <nav class="brown">
        <div id="navbar">
            <div class="nav-wrapper">
                <div class="container">
                    <router-link to="/" class="brand-logo left">
                        
                        Link2Beans                        
                    </router-link>
                    <ul class="right">
                        <li v-if="isLoggedIn"><i class="fa fa-user"></i><span class="email white-text">{{currentUser}}</span></li>
                        <li v-if="isLoggedIn"><router-link to="/">Cafes List</router-link></li>
                        <li v-if="!isLoggedIn"><router-link to="/login">Login</router-link></li>
                        <li v-if="!isLoggedIn"><router-link to="/register">Register</router-link></li>
                        <li v-if="isLoggedIn"><button v-on:click="logout" class="btn white brown-text">Logout</button></li>
                    </ul>
                </div>
            </div>
        </div>        
    </nav>    
</template>

<script>
import firebase from 'firebase';

export default {
    name: 'navbar',
    data(){
        return {
            isLoggedIn: false,
            currentUser: false
        };
    },
    created(){
        if(firebase.auth().currentUser){
            this.isLoggedIn = true;
            this.currentUser = firebase.auth().currentUser.email;
        }
    },
    methods:{
        logout: function(){
            firebase
                .auth()
                .signOut()
                .then(() => {
                    this.$router.go({path: this.$router.path});
            });
        }
    }   
}
</script>

<style scoped>
.email{
    padding-right:5px;
    padding-left:5px;
}
</style>




