<template>
    <div id="view-cafe">
        <ul class="collection with-header">
            <li class="collection-item"><div class="chip"><i class="fa fa-train"></i> {{station}}</div></li>
            <li class="collection-header">
                <h4><i class="fa fa-coffee"></i> {{name}}</h4>
            </li>
            <li class="collection-item"><i class="fa fa-map-marker"></i> {{address}}</li>
        </ul>    
        <router-link to="/" class = "btn brown">Back</router-link>    
        <button @click="deleteCafe" class = "btn brown">Delete</button>    
        <div class="fixed-action-btn">
            <router-link v-bind:to="{name: 'edit-cafe', params: {cafe_id:cafe_id}}" class="btn-floating btn-large brown">
            <i class="fa fa-pencil"></i>

            </router-link>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'view-cafe',
    data () {
        return{
            cafe_id: null,
            name: null,
            address: null,
            station: null
        }
    },
    beforeRouteEnter(to, from, next){
        db.collection('cafes').where('cafe_id', '==', to.params.cafe_id).get().then(querySnapshot => {
            querySnapshot.forEach(doc => {
                next(vm => {
                    vm.cafe_id = doc.data().cafe_id;
                    vm.address = doc.data().address,
                    vm.name = doc.data().name,
                    vm.station = doc.data().station
                })
            })
        })
    },
    watch: {
        '$route': 'fetchData'
    },
    methods: {
        fetchData(){
            db.collection('cafes').where('cafe_id', '==', this.$route.params.cafe_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    this.cafe_id = doc.data().cafe_id;
                    this.address = doc.data().address,
                    this.name = doc.data().name,
                    this.station = doc.data().station
                })
            })
        },
        deleteCafe(){
            if(confirm('Are you sure?')){
                db.collection('cafes').where('cafe_id', '==', this.$route.params.cafe_id).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    doc.ref.delete();
                    this.$router.push('/')
                    })
                })
            }
        } 
    }
}
</script>


