<template>
    <div id="new-cafe">
        <h4>New Cafe</h4>
        <div class="row">
            <form @submit.prevent="saveCafe" class="col s12">
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="cafe_id" required>
                        <label>Cafe ID</label>
                    </div>
                </div>                
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="name" required>
                        <label>Name</label>
                    </div>
                </div>
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="address" required>
                        <label>Address</label>
                    </div>
                </div>    
                <div class="row">
                    <div class="input-field col s12">
                        <input type="text" v-model="station" required>
                        <label>Station</label>
                    </div>
                </div>    
                <button type="submit" class="btn brown">Submit</button>   
                <router-link to="/" class="btn brown">Cancel</router-link>     
            </form>
        </div>
    </div>
</template>

<script>
import db from './firebaseinit'
export default {
    name: 'new-cafe',
    data () {
        return{
            cafe_id: null,
            name: null,
            address: null,
            station: null

        }
    },
    methods: {
        saveCafe(){
            db.collection('cafes').add({
                cafe_id: this.cafe_id,
                name: this.name,
                address: this.address,
                station: this.station
            })
            .then(docRef => this.$router.push('/'))
            .catch(error => console.log(err))
        }
        
    }
}
</script>


